  // preview the uploaded image
  async preview(files) {
    await this.authService.getUserimg().subscribe((data) => {
      console.log(data);
      console.log(data.imgre);
      if (data.imgre) {
        if (files.length === 0) {
          return;
        }
        let mimeType = files[0].type;
        if (mimeType.match(/image\/*/) == null) {
          return;
        }
        let reader = new FileReader();
        this.imagePath = files;
        reader.readAsDataURL(files[0]);
        reader.onload = (_event) => {
          this.imgURL = reader.result;
        };
        if (this.currentUser.msg.img_state == 1) {
          this.uploader.uploadAll();
        } else if (this.currentUser.msg.img_state == 0) {
          this.blockwarning();
        }

      } else {
        this.warning = true;
        this.warning_message = "You can not upload image because of block";
        this.currentUser.msg.img_state = 0;
        this.imgURL = "/assets/image/login.jpg";
      }
    });
  }
  // logout
