  public deletePostConfirm(id) {
    const message = `Are you sure you want to delete this?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(ConfirmModalComponent, {
      maxWidth: "400px",
      data: dialogData,
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res == true) {
        this.deleteMessage(id);
      }
    });
  }

  public disputePostConfirm(message_id, msg, userId) {
    const message = `Are you sure you want to dispute this?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(ConfirmModalComponent, {
      maxWidth: "400px",
      data: dialogData,
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res == true) {
        this.dispute(message_id, msg, userId);
      }
    });
  }

  deleteMessage(id) {
    this.authService.deleteMessage({
      messageId: id,
      clientId: this.currentUser.msg._id,
    });
  }

  dispute(message_id, message, userId) {
    console.error(
      ">>>>>>>>>>>>>>dispute",
      message_id,
      message,
      userId,
      this.currentUser.msg._id
    );
    this.authService
      .dispute({
        message_id: message_id,
        message: message,
        userId: userId,
        clientId: this.currentUser.msg._id,
      })
      .subscribe((res) => {
        if (res.message == "success") {
          alert("Your dispute transferred successfully");
        }
      });
  }

  openHelpSubscribe() {
    this.warning = true;
    this.warning_message =
      '"Posts Subscribers Room" :  Allows you to see all the messages posted by the users to whom we wish to subscribe.';
  }
  openHelpMain() {
    this.warning = true;
    this.warning_message =
      '"Main ChatRoom" : Allows you to see all messages posted by all users.';
  }
  openHelpResonse() {
    this.warning = true;
    this.warning_message =
      '"Responses Received Room" : "Reserved for subscribers" Allows you to see the responses received to our messages.';
  }
