import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import { AuthGuard } from "./shared/auth.guard";
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { ForgetComponent } from './forget/forget.component';
import { WifpaypalComponent } from './wifpaypal/wifpaypal.component';
import { Wifpaypal30Component } from './wifpaypal30/wifpaypal30.component';
import { SubscriptionComponent } from './subscription/subscription.component';
import { ConditionuseComponent } from './conditionuse/conditionuse.component';
import { LegalnoticeComponent } from './legalnotice/legalnotice.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { ConfirmComponent } from './confirm/confirm.component';

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'confirm/:id', component: ConfirmComponent },
  { path: 'forget', component: ForgetComponent },
  {
    path: 'main/:id',
    component: MainComponent, canActivate: [AuthGuard]
  },
  { path: 'getsubscription', component: SubscriptionComponent },
  { path: 'subscription15', component: WifpaypalComponent },
  { path: 'subscription30', component: Wifpaypal30Component },
  { path: 'condition', component: ConditionuseComponent},
  { path: 'privacy', component: PrivacyComponent },
  { path: 'legal', component: LegalnoticeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
