import { Component, OnInit, ElementRef, ViewChild, ViewEncapsulation} from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, CheckboxControlValueAccessor } from '@angular/forms';
import { AuthService } from '../shared/auth.service';
import { User } from '../shared/user';
import { LoadingService } from '../loading.service';

@Component({
  selector: 'app-forget',
  templateUrl: './forget.component.html',
  styleUrls: ['./forget.component.scss']
})
export class ForgetComponent implements OnInit {
  isLoading = false;
  updateForm: FormGroup;
  loginForm: FormGroup;
  email = new FormControl('');
  password = new FormControl('');
  cVal: any;
  recaptcha: any[];
  recover_email: '';

  // verify:string;
  flag = 0;
  constructor(
    private auth: AuthService,
    private formBuilder: FormBuilder,
    public loadingService: LoadingService,
    private router: Router
  ) { this.isLoading = true; }
  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      email: this.email,
    });
    this.updateForm = this.formBuilder.group({
      password: this.password,
    });
  }
  login() {
    this.flag = 1;
    this.recover_email = this.loginForm.value.email;
    this.auth.forget(this.loginForm.value);
  }
  verifyFunc(val) {
    console.log(val, this.recover_email);
    console.log('1');
    this.auth.verify_val_code(val, this.recover_email).subscribe((res) => {
      console.log(res);
      if(res.success === true) {
        this.flag = 2;
      }
    });
  }
  resolved(captchaResponse: any[]) {
    this.recaptcha = captchaResponse;
    console.log(this.recaptcha);
  }
  updateFunc() {
    if (!this.recaptcha) {
      alert('You have to check robot verifycation');
     } else {
    this.cVal = {
      "email" : this.recover_email,
      "password" : this.updateForm.value.password,
    }
    this.auth.updateFunc(this.cVal).subscribe((res) => {
      console.log(res);
      if(res.success == true) {
        this.router.navigate(['/login']);
      }
    });
  }
  }
}
