export default "header {\n  position: relative;\n  background-color: #04af74 !important;\n  padding-bottom: 15px;\n}\n\nimg {\n  height: 50%;\n  padding: 0;\n}\n\nheader h1 {\n  position: absolute;\n  color: white;\n  font-size: 25px;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.row {\n  height: calc(100% - 100px);\n  width: 100%;\n  margin: 0px;\n}\n\n.form-wrap {\n  text-align: center;\n  background: url('register.jpg'), rgba(255, 255, 255, 0.3);\n  background-blend-mode: lighten;\n  background-size: cover;\n  border-radius: 10%;\n  width: 600px;\n  height: 720px;\n  padding: 5% 5%;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.form-wrap .input-wrap {\n  margin: 5px 0;\n}\n\n.form-wrap .mat-form-field {\n  width: 100%;\n}\n\nh1 {\n  color: #3867d5;\n  font-size: 46px;\n}\n\n.mat-form-field-wrapper {\n  padding-bottom: 0px !important;\n}\n\n.warning-div {\n  position: fixed;\n  padding: 15px;\n  background-color: #49a3f2;\n  color: white;\n  top: 0;\n  width: 100%;\n  z-index: 100;\n  text-align: center;\n  font-size: 20px;\n}\n\n.warning-button {\n  border-radius: 4px;\n  background-color: white;\n  border: none;\n  padding: 3px 8px;\n  float: right;\n}\n\n.cancel_button {\n  padding: 5px 12px;\n  border: none;\n  border-radius: 4px;\n  background-color: #e62c22;\n  float: right;\n  color: white;\n}\n\n/*mobile response*/\n\n@media (max-width: 1200px) {\n  .form-wrap {\n    width: 550px;\n  }\n\n  h1 {\n    font-size: 40px;\n  }\n}\n\n@media (max-width: 767px) {\n  .form-wrap {\n    margin-top: 0px;\n    width: 90%;\n    height: 450px;\n  }\n\n  h1 {\n    font-size: 35px;\n  }\n}\n\n@media (max-width: 400px) {\n  .form-wrap {\n    width: 98%;\n  }\n\n  h1 {\n    font-size: 30px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"