import { Component, OnInit } from '@angular/core';
import { AuthService } from '../shared/auth.service';
import { ActivatedRoute, Router } from '@angular/router';
import { LoadingService } from '../loading.service';

@Component({
  selector: 'app-confirm',
  templateUrl: './confirm.component.html',
  styleUrls: ['./confirm.component.scss']
})
export class ConfirmComponent implements OnInit {

  public confirmationID: any;

  constructor(
    private auth: AuthService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    public loadingService: LoadingService
  ) { }

  ngOnInit() {
    this.confirmationID = this.activatedRoute.snapshot.paramMap.get('id');
    console.log(this.confirmationID);
    this.confirmEmail(this.confirmationID);
  }

  public confirmEmail(confirmID): void {
    this.auth.confirmEmail(confirmID).subscribe((res) => {
      this.router.navigate(['/login']);
    })
  }

  public connection() {
    this.router.navigate(['/login']);
  }
}
