import { Component, OnInit, NgZone } from '@angular/core';
import { IPayPalConfig, ICreateOrderRequest } from 'ngx-paypal';
import { Location } from '@angular/common';
import { AuthService } from '../shared/auth.service';
import { Router } from '@angular/router';
import { LoadingService } from '../loading.service';

@Component({
  selector: 'app-wifpaypal',
  templateUrl: './wifpaypal.component.html',
  styleUrls: ['./wifpaypal.component.scss']
})
export class WifpaypalComponent implements OnInit {

  public payPalConfig?: IPayPalConfig;

  public warning = false;
  public warning_message = '';
  private currentUser = JSON.parse(localStorage.getItem('current_user'));
  private userId = String;
  public showSuccess = false;

  constructor(
    private _location: Location,
    private authService: AuthService,
    public loadingService: LoadingService,
    public router: Router
  ) { }

  ngOnInit() {
    this.initConfig();
    this.currentUser = JSON.parse(localStorage.getItem('current_user'));
    this.userId = JSON.parse(localStorage.getItem('current_user')).msg._id;
    console.log(this.userId);
    console.log(this.currentUser);
  }

  private initConfig(): void {
    this.payPalConfig = {
      currency: 'EUR',
      clientId: 'AXGtxaZPhtTY3fQCa99vEVjKjQG5sCrWhwFcdPhQNRI9fLo9M0WeM0F5tIcOR1KdJ-TRX0F7Rc9b6CJL',
      createOrderOnClient: (data) => <ICreateOrderRequest>{
        intent: 'CAPTURE',
        purchase_units: [
          {
            amount: {
              currency_code: 'EUR',
              value: '9.99',
              breakdown: {
                item_total: {
                  currency_code: 'EUR',
                  value: '9.99'
                }
              }
            },
            items: [
              {
                name: 'Enterprise Subscription',
                quantity: '1',
                category: 'DIGITAL_GOODS',
                unit_amount: {
                  currency_code: 'EUR',
                  value: '9.99',
                },
              }
            ]
          }
        ]
      },
      advanced: {
        commit: 'false',
      },
      style: {
        label: 'paypal',
        size: 'small',
        shape: 'pill',
        color: 'blue',
        layout: 'horizontal',
        tagline: false
      },

      onApprove: (data, actions) => {
        this.warning = false;
        console.log('onApprove - transaction was approved, but not authorized', data, actions);
        actions.order.get().then(details => {
          console.log('onApprove - you can get full order details inside onApprove: ', details);
        });
      },
      onClientAuthorization: (data) => {
        console.log('onClientAuthorization - you should probably inform your server about completed transaction at this point', data);
        this.showSuccess = true;
        console.log(this.warning);
        this.warning_message = 'Congratelation!\nYou have the bonus from now!';
        console.log(this.warning_message);
        this.authService.subscription15().subscribe((data) => {
          console.log(data);
          if (data == 'faild' || data == null) {
          } else {
            let userdata = {
              msg: data
            }
            localStorage.setItem('current_user', JSON.stringify(userdata));
            this.warning = false;
            // this.ngZone.run(() => this.router.navigate(['main/' + this.userId])).then();
          }
        });
        // this.router.navigate(['main/' + this.userId]);
      },
      onCancel: (data, actions) => {
        this.warning = false;
        console.log('OnCancel', data, actions);
      },
      onError: err => {
        this.warning = false;
        console.log('OnError', err);
      },
      onClick: (data, actions) => {
        console.log('onClick', data, actions);
        this.warning = true;
        this.warning_message = 'Please wait "Winorball subscription verification and confirmation"';
        // this.warning_message = '... please wait ...';
      },
    };
  }
  cancel() {
    this._location.back();
  }

  confirm() {
    // console.log(this.currentUser.msg._id);
    // this.router.navigate(['main/' + this.authService.getCurrentUser().msg._id]);
    this.warning_message = '';
    if (this.showSuccess) {
      this._location.back();
    } else {
      this.warning = true;
      this.warning_message = 'subscription failed'
      this._location.back();
    }
  }

}
