import { Component, OnInit, ElementRef, ViewChild, ViewEncapsulation} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { AuthService } from '../shared/auth.service';
import { LoadingService } from '../loading.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  // encapsulation : ViewEncapsulation.None
})

export class LoginComponent implements OnInit {
  isLoading = false;
  loginForm: FormGroup;
  email = new FormControl('');
  password = new FormControl('');
  recaptcha: any[];
  pageIn = 1;
  warning: boolean = false;
  warning_message: String;
  verifyId: String;
  public user_id: any;

  constructor(
    private auth: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    public loadingService: LoadingService,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    if(this.auth.getToken() !== null && this.auth.getCurrentUserId() !== null) {
      this.router.navigate(['/main/' + this.auth.getCurrentUserId()]);
    }
    this.verifyId = localStorage.getItem('verify');
    console.log(this.verifyId);
    this.activatedRoute.queryParams.subscribe(params => {
        console.log(" this is the verifyId..........");
        console.log(this.verifyId);
        if (params['verify']) {
          this.verifyId = params['verify'];
          localStorage.setItem('verify', params['verify']);
          console.log(params['verify']);
          this.warning = true;
          this.warning_message = 'Your accont is confirmed, you can now login';
          this.auth.confirmEmail(params['verify']);
        } else {
          if (this.verifyId === 'need') {
            this.warning = true;
            this.warning_message = 'Please confirm your account an email has been sent to you';
          }
        }
      });
    this.isLoading = true;
    // if (this.auth.loggedIn) {
    //   this.router.navigate(['/']);
    // }
    this.loginForm = this.formBuilder.group({
      email: this.email,
      password: this.password
    });
    this.pageIn = 1;
  }
  resolved(captchaResponse: any[]) {
    this.recaptcha = captchaResponse;
    console.log(this.recaptcha);
  }

  login() {
    
    // this.auth.signIn(this.loginForm.value);
    if (!this.recaptcha) {
     alert('You have to check robot verifycation');
    } else {
      if (this.verifyId === 'need') {
        this.warning = true;
        this.warning_message = 'Please confirm your account an email has been sent to you';
      } else {
        console.log(this.loginForm.value);
        this.auth.signIn(this.loginForm.value);
      }
    }
  }

  closeWarningbutton() {
    this.warning = false;
    this.warning_message = '';
  }
}
