import { Component, OnInit, ElementRef, ViewChild, ViewEncapsulation} from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '../shared/auth.service';
import { isEmpty } from 'rxjs/operators';
import { LoadingService } from '../loading.service';
const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
  encapsulation : ViewEncapsulation.None,
})
export class RegisterComponent implements OnInit {
  registerForm: FormGroup;
  isLoading: boolean = false;
  recaptcha: any[];
  warning: boolean = false;
  warning_message: String;
  email = new FormControl('', [
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100),
    Validators.pattern(EMAIL_REGEX)
  ]);
  name = new FormControl('', [
    Validators.required,
    Validators.minLength(5),
    Validators.maxLength(100),
  ]);
  pseduo = new FormControl('', [
    Validators.required,
    Validators.minLength(3),
    Validators.maxLength(100),
  ]);
  password = new FormControl('', [
    Validators.required,
    Validators.minLength(5)
  ]);
  firstname = new FormControl('', [
    Validators.required,
    Validators.minLength(5),
    Validators.maxLength(100),
  ]);
  constructor(
    private auth: AuthService,
    private formBuilder: FormBuilder,
    public loadingService: LoadingService,
    private router: Router
  ) { }

  ngOnInit() {
    this.warning = false;
    this.warning_message = '';
    this.isLoading = true;
    // if (this.auth.loggedIn) {
    //   this.router.navigate(['/']);
    // }
    // this.addRecaptchaScript();
    this.registerForm = this.formBuilder.group({
      name: this.name,
      firstname: this.firstname,
      email: this.email,
      pseduo: this.pseduo,
      password: this.password
    });
  }

  closeWarningbutton() {
    this.warning = false;
    this.warning_message = '';
  }
  resolved(captchaResponse: any[]) {
    this.recaptcha = captchaResponse;
    console.log(this.recaptcha);
  }
  register() {
    if (!this.recaptcha) {
      alert('You have to check robot verifycation');
    } else {
      this.auth.signUp(this.registerForm.value).subscribe((res) => {
        if (res.result) {
          console.log(res);
          this.registerForm.reset();
          this.warning = true;
          this.warning_message = 'Registered Successfully, please check your mailbox and confirm your registeration'
          localStorage.setItem('verify', 'need');
          this.router.navigate(['login']);
        }
        if (res.error) {
          console.log(res.error);
          this.warning = true;
          if (res.error.errors.hasOwnProperty('name')) {
            this.warning_message = 'Name already used "please specify another one"';
          } else if(res.error.errors.hasOwnProperty('email')) {
            this.warning_message = 'E-mail already used "please specify another one"';
          } else if(res.error.errors.hasOwnProperty('pseduo')) {
            this.warning_message = 'Nickname already used "please specify another one"';
          } else {
            this.warning_message = 'Registration error "please follow the instructions to complete the registration"';
          }
          console.log("you need to resiger again");
        }
        // this.router.navigate(['login']);
      });
    }
  }
}
