import { Injectable } from "@angular/core";
import * as io from "socket.io-client";
import { Observable } from "rxjs/Observable";
import { HttpModule, Headers, Http } from "@angular/http";
import {environment} from "src/environments/environment";
@Injectable({
  providedIn: "root",
})
export class WebchatService {
  // private socket = io("http://localhost:3000");
  private socket = io(environment.authUrl);
  constructor() { }
  // send my online state
  iamonline(data) {
    this.socket.emit("iamonline", data);
  }
  // check the online counters
  onlineusers() {
    const observable = new Observable<any>((observer) => {
      this.socket.on("onlinecounter", (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }
  // logout and offline users
  offline(data) {
    this.socket.emit("iamoffline", data);
  }
  // sent message
  sendMessage(data) {
    this.socket.emit("message", data);
  }
  // send answer
  sendAnswer(data) {
    console.log("****************send message", data);
    this.socket.emit("answer", data);
  }
  // get new answer
  newAnswer() {
    const observable = new Observable<{
      message_id: String;
      owner: String;
      client: String;
      relative: String;
      answer_state: String;
      answer: Array<{ message: String; videoLink: String; sendTime: String }>;
      userdetail: Array<{
        _id: String;
        name: String;
        email: String;
        pseduo: String;
        days_15: String;
        days_30: String;
        pay_count: String;
        pay_cost: String;
        res_day: String;
        photoUrl: String;
      }>;
    }>((observer) => {
      this.socket.on("new answer", (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }
  // get the message
  newMessageReceived() {
    const observable = new Observable<{
      _id: String;
      userId: String;
      name: String;
      useremail: String;
      photoUrl: String;
      message: String;
      videoLink: String;
      sendTime: String;
      state: number;
      follows: number;
      followers: Array<{ useremail: String; state: Number }>;
      comments: Array<{
        comment_user: String;
        comment_message: String;
        comment_video: String;
      }>;
      deActivaters: Array<[]>;
      userdetail: Array<{
        userId: String;
        pseduo: String;
        email: String;
        photoUrl: String;
        img_state: Number;
      }>;
    }>((observer) => {
      this.socket.on("new message", (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }

  // send the subscribe
  subscribePost(data) {
    console.log(data);
    this.socket.emit("subscribePost", data);
  }
  // get the susbscribe
  newSubscribePost() {
    const observable = new Observable<any>((observer) => {
      this.socket.on("new subscribePost", (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }

  // send the unsubscbribe
  unsubscribePost(data) {
    this.socket.emit("unsubscribePost", data);
  }
  // get the unsusbscribe
  newUnsubscribePost() {
    const observable = new Observable<any>((observer) => {
      this.socket.on("new unsubscribePost", (data) => {
        observer.next(data);
      });
      return () => {
        this.socket.disconnect();
      };
    });
    return observable;
  }
  // blocking the user
  messageblock(data) {
    this.socket.emit("block message", data);
  }
  // unblocking the user
  messageunblock(data) {
    this.socket.emit("unblock message", data);
  }
}
