import { Injectable, Output, EventEmitter } from "@angular/core";
import { User } from "./user";
import { Observable, throwError, Subscriber } from "rxjs";
import { catchError, map } from "rxjs/operators";
import {
  HttpClient,
  HttpHeaders,
  HttpErrorResponse,
} from "@angular/common/http";
import { Router } from "@angular/router";

import {environment} from "src/environments/environment";
// import * as io from "socket.io-client";
@Injectable({
  providedIn: "root",
})
export class AuthService {

  startpoint: string = environment.apiUrl;
  endpoint: string = environment.authUrl;

  headers = new HttpHeaders().set("Content-Type", "application/json");
  currentUser;
  socket: any;
  readonly uri: string = "ws://api.winorball.com";
  // readonly uri: string = "ws://localhost:3000";
  constructor(private http: HttpClient, public router: Router) { }
  // Sign-up
  signUp(user: User): Observable<any> {
    let api = `${this.endpoint}/register-user`;
    console.log("sign up api", api);
    return this.http.post(api, user).pipe(catchError(this.handleError));
  }

  // Sign-in
  signIn(user: User) {
    console.log(user);
    return this.http
      .post<any>(`${this.endpoint}/signin`, user)
      .subscribe((res: any) => {
        console.log(res);
        if (res.error || res.noconfirm) {
          console.log("you need to verify");
          alert("Wrong user account. please login correct");
        } else {
          localStorage.setItem("access_token", res.token);
          // this.emit('user name added',res._id);
          this.getUserProfile(res._id).subscribe((res: any) => {
            this.currentUser = res;
            // for the time invterval message
            localStorage.setItem("robot_last_recorded_time", "0");
            localStorage.setItem("robot_first_recorded_time", "0");
            localStorage.setItem("robot_ruletime", "3600000");
            localStorage.setItem("robot_time_interval", "60000");
            localStorage.setItem("robot_number", "5");
            localStorage.setItem("robot_count", "0");

            localStorage.setItem("current_user", JSON.stringify(res));
            localStorage.setItem("user_id", JSON.stringify(res.msg._id));
            this.router.navigate(["main/" + res.msg._id]);
            // this.router.navigateByUrl('main']);
          });
        }
      });
  }
  // forget
  forget(email: String) {
    return this.http
      .post<any>(`${this.endpoint}/forget`, email)
      .subscribe((res: any) => {
        // localStorage.setItem('access_token', res.token)
        // this.emit('user name added',res._id);
        this.getUser_Profile(res._id).subscribe((res) => {
          this.currentUser = res;
          // this.router.navigateByUrl('main']);
        });
      });
  }
  update(user: User) {
    return this.http
      .post<any>(`${this.endpoint}/updatePassword`, user)
      .subscribe((res: any) => {
        // localStorage.setItem('access_token', res.token)
        // this.emit('user name added',res._id);
        this.router.navigate(["login"]);
      });
  }
  getToken() {
    return localStorage.getItem("access_token");
  }

  get isLoggedIn(): boolean {
    let authToken = localStorage.getItem("access_token");
    return authToken !== null ? true : false;
  }

  doLogout() {
    let removeToken = localStorage.removeItem("access_token");
    if (removeToken == null) {
      localStorage.clear();
      this.router.navigate(["login"]);
    }
  }

  // User profile
  getUserProfile(id): Observable<any> {
    let api = `${this.endpoint}/user-profile/${id}`;
    return this.http.get(api, { headers: this.headers }).pipe(
      map((res: Response) => {
        return res || {};
      }),
      catchError(this.handleError)
    );
  }
  getUser_Profile(id): Observable<any> {
    let api = `${this.endpoint}/userProfile/${id}`;
    return this.http.get(api, { headers: this.headers }).pipe(
      map((res: Response) => {
        return res || {};
      }),
      catchError(this.handleError)
    );
  }
  // Error
  handleError(error: HttpErrorResponse) {
    let msg = "";
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }
  // get the current user
  getCurrentUser() {
    this.currentUser = JSON.parse(localStorage.getItem("current_user"));
    return this.currentUser;
  }
  // get the current user id
  getCurrentUserId() {
    return JSON.parse(localStorage.getItem("user_id"));
  }
  // update the user image path
  updateUserImage(user_id, imageName) {
    let api = `${this.endpoint}/photo/${user_id}/${imageName}`;
    return this.http.get(api, { headers: this.headers }).subscribe((res) => {
      console.log(res);
    });
  }
  // get the block users
  getBlockUsers(): Observable<any> {
    let api = `${this.endpoint}/blockusers/${this.getCurrentUserId()}`;
    return this.http.get(api);
  }

  // get all chatrooms messages
  getChatRoomsMessage(): Observable<any> {
    let api = `${this.startpoint}/chatroom/${this.currentUser.msg._id}`;
    return this.http.get(api);
  }

  // get the response received
  getResponseReceived(): Observable<any> {
    let api = `${this.startpoint}/response/${this.currentUser.msg._id}`;
    return this.http.get(api);
  }

  // get the subscribe received
  getMySubscribers(): Observable<any> {
    let api = `${this.startpoint}/subscribe/${this.currentUser.msg._id}`;
    return this.http.get(api);
  }
  // update myinfo for subscription 15
  subscription15() {
    let api = `${this.endpoint}/subscription15/${this.getCurrentUserId()}`;
    return this.http.get(api);
  }

  // update myinfo for subscription 30
  subscription30() {
    let api = `${this.endpoint}/subscription30/${this.getCurrentUserId()}`;
    return this.http.get(api);
  }

  // confirm the user information
  confirmEmail(confirmID) {
    let api = `${this.endpoint}/confirm/${confirmID}`;
    return this.http.get(api, { headers: this.headers }).pipe(
      map((res: Response) => {
        console.log(res);
        return res || {};
      })
    );
  }

  // refresh the message
  messageRefresh(uid) {
    let api = `${this.endpoint}/refresh/${uid}`;
    return this.http.get(api, { headers: this.headers }).subscribe((res) => {
      console.log(res);
    });
  }

  // check user image upload state
  getUserimg(): Observable<any> {
    let api = `${this.startpoint}/getUserimg/${this.currentUser.msg._id}`;
    return this.http.get(api);
  }

  //delete the message
  deleteMessage(data) {
    let api = `${this.startpoint}/deleteMessage`;
    console.log("this is delete message api", data);
    return this.http.post<any>(api, data).subscribe((res) => {
      if (res.message == "success") {
        window.location.reload();
      }
    });
  }
  dispute(data): Observable<any> {
    let api = `${this.startpoint}/dispute`;
    console.log("this is dispute data api", data);
    return this.http.post(api, data).pipe(
      map((res) => {
        return res;
      })
    );
  }

  verify_val_code(val, email): Observable<any> {
    return this.http
      .post(`${this.endpoint}/verify_val_code`, { email: email, val_code: val })
      .pipe(
        map((res: Response) => {
          console.log(res);
          return res || {};
        })
      );
  }

  updateFunc(data): Observable<any> {
    console.log(data);
    return this.http
      .post(`${this.endpoint}/updatePassword`, {
        email: data.email,
        updatePassword: data.password,
      })
      .pipe(
        map((res: Response) => {
          console.log(res);
          return res || {};
        })
      );
  }
}
