import { BrowserModule } from '@angular/platform-browser';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

// by ftf

import { FileUploadModule } from 'ng2-file-upload';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { MaterialModule } from './material/material.module';
import { LoadingComponent } from './loading/loading.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MainComponent } from './main/main.component';
import { WifpaypalComponent } from "./wifpaypal/wifpaypal.component";

import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AuthInterceptor } from "./shared/authconfig.interceptor";

import { ForgetComponent } from './forget/forget.component';
import { RecaptchaModule } from 'ng-recaptcha';
import { NgxTimerModule } from 'ngx-timer';
import { NgxPayPalModule } from 'ngx-paypal';
import { SubscriptionComponent } from './subscription/subscription.component';
import { Wifpaypal30Component } from './wifpaypal30/wifpaypal30.component';
import { ConditionuseComponent } from './conditionuse/conditionuse.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { LegalnoticeComponent } from './legalnotice/legalnotice.component';
import { ConfirmComponent } from './confirm/confirm.component';
import { ConfirmModalComponent } from './confirm-modal/confirm-modal.component';

export function TranslationLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    LoadingComponent,
    MainComponent,
    ForgetComponent,
    WifpaypalComponent,
    SubscriptionComponent,
    Wifpaypal30Component,
    ConditionuseComponent,
    PrivacyComponent,
    LegalnoticeComponent,
    ConfirmComponent,
    ConfirmModalComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RecaptchaModule,
    NgxTimerModule,
    NgxPayPalModule,
    FileUploadModule,
    TranslateModule.forRoot({
      loader: { provide: TranslateLoader, useFactory: TranslationLoaderFactory, deps: [HttpClient] }
    }),
  ],
  exports: [
    LoadingComponent
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    }
  ],
  entryComponents: [
    ConfirmModalComponent
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  bootstrap: [AppComponent]
})
export class AppModule { }
